.users_container{
    text-align: left;
    color: wheat;
    overflow-x: none;
    background-color: rgba(30, 66, 61, 0.864);
    margin-right: 10px;

}
.users{
    border-bottom: groove;
    border-radius: 1px;
    border-color: rgb(170, 170, 170);
    border-width: 1px;
    height: 50px;
}
.users img{
    width: 50px;
    height: 50px;
}
.user_data {
    display: inline-block;
    margin-left: 20px;
    /* margin-bottom: 10px;
    margin-top: 10px; */

}
.users button {
    width: 90px;
    height: 40px;
    border-radius: 30px;
    border-style: none;
    background-color: rgb(44, 48, 47);
    color: white;
    font-weight: 300;
    font-size: 15px;
    display: inline;
    float: right;
    margin-right: 20px;
    box-shadow: 1px 1px 2px 1px rgb(31, 9, 31);
}
.users button :hover    {
    box-shadow: 2px 2px 5px 3px rgb(233, 142, 233);
} ;

.selected_paige{
    font-weight: bold;
}
.preloader{
    width: 100px;
    height: 70px;
    position: relative;
    margin-top: 30%;
    margin-left: 50%;
}
.paginator {

}
.paginator_container {
    background-color: rgba(12, 29, 27, 0.775);
    height: 50px;
}
.users_container button {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
    width: 70px;
    height: 30px;
    background-color: rgb(39, 179, 113);
    border-radius: 5px;
    border-style: none;
    color: white;
    font-size: 15px;
}
.users_list {
    margin-top: 30px;
    
}